.cssPoorVision {
  height: 100%;
  overflow: hidden;
  &-inner {
    height: 100%;
    transform-style: preserve-3d;
    perspective: 1px;
    // 很奇怪的bug ，把页面移动到body下就不会
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
}
.cssPoorVision-fixed {
  transform-style: preserve-3d;
  height: 50%;
  background-size: cover;
  background-position: center bottom;
  position: relative;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 40px;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    left: -6px; //优化
    top: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-position: center center;
    background-image: url('./img/bg.jpg');
    z-index: -1;
    transform-origin: center bottom;
    transform: translateZ(-1px) scale(2);
  }
}
.cssPoorVision-details {
  background-color: #431616;
  color: #fff;
  padding: 20px;
  height: 100%;
  position: relative;
  z-index: 2;
}
