.cssBackPosition3 {
  position: relative;
  height: 100%;
}
.cssBackPosition3-inner {
  height: 100%;
  position: relative;
  padding-top: 0.1px;
}
.cssBackPosition3-fixed {
  background-attachment: fixed;
  background-size: cover;
  background-position: center center;
  background-image: url('./img/bg.jpg');
  position: fixed;
  // position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #000;
  color: #fff;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  button {
    cursor: pointer;
  }
}
.cssBackPosition3-details {
  width: 100%;
  color: #fff;
  position: relative;
  z-index: 9;
  //适合全屏使用：margin-top: 100vh;
  // margin-top: calc(100vh - 60px - 64px);
  &__inner {
    width: 100%;
    position: absolute;
    z-index: 9;
    display: flex;
    flex-direction: column;
    background-color: #393939;
    box-sizing: border-box;
    padding: 20px;
    line-height: 30px;
    height: 50vh;
    margin-top: 100vh;
  }
}

.fl-layout__content-body--inner .cssBackPosition3-inner {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  transform: scale(1, 1);
  pointer-events: auto;
}
.fl-layout__content-body--inner .cssBackPosition3-details__inner {
  margin-top: calc(100vh - 60px - 64px);
}
