.flmp {
  &-media {
    display: flex;
    height: 100%;
  }
  &-media__main {
    flex-grow: 1;
    flex-shrink: 1;
    border-left: 1px solid #ddd;
    margin-left: -1px;
    padding-left: 20px;
  }

  &-media__menu {
    max-width: 200px;
    flex-grow: 0;
    flex-shrink: 0;
    text-align: right;
    padding-right: 20px;
    border-right: 1px solid #ddd;
    box-sizing: border-box;
    &-list{
      margin: -8px 0;
      display: flex;
      flex-direction: column;
    }
    &-item{
      line-height: 20px;
      font-size: 14px;
      padding-top: 8px;
      padding-bottom: 8px;
      font-weight: 500;
      display: inline-block;
      color: #666;
      &:hover,&.is-actived{
        color: #1890ff;
      }
      a{
        color: inherit;
      }
    }
  }
}
