.CssTableFlex {
  display: table;
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  &-cell {
    display: table-cell;
    border: 1px solid #ddd;
    padding: 12px;
  }
  &-flex {
    display: flex;
    font-size: 12px;
    justify-content: space-between;
    &__auto {
      flex-shrink: 1;
      flex-grow: 1;
      border: 1px solid #ddd;
      padding: 12px;
    }
    &__fixed {
      flex-shrink: 0;
      flex-grow: 0;
      width: 200px;
      border: 1px solid #ddd;
      padding: 12px;
    }
  }
}
