$CssTiangleWidth: 200;
$CssTiangleHeight: $CssTiangleWidth;
// 开方
@function sqrt($num) {
  $temp: 1;
  @while abs($temp - $num / $temp) > 1e-6 {
    $temp: ($temp + $num/$temp) / 2;
  }
  @return $temp;
}
// 幂操作
@function pow($number, $exp) {
  $value: 1;
  @if $exp > 0 {
    @for $i from 1 through $exp {
      $value: $value * $number;
    }
  } @else if $exp < 0 {
    @for $i from 1 through -$exp {
      $value: $value / $number;
    }
  }
  @return $value;
}
// 阶乘
@function fact($number) {
  $value: 1;
  @if $number > 0 {
    @for $i from 1 through $number {
      $value: $value * $i;
    }
  }
  @return $value;
}
// π 的约值
@function pi() {
  @return 3.14159265359;
}
@function rad($angle) {
  $unit: unit($angle);
  $unitless: $angle / ($angle * 0 + 1);
  // If the angle has 'deg' as unit, convert to radians.
  @if $unit == deg {
    $unitless: $unitless / 180 * pi();
  }
  @return $unitless;
}
// sin 值
@function sin($angle) {
  $sin: 0;
  $angle: rad($angle);
  // Iterate a bunch of times.
  @for $i from 0 through 10 {
    $sin: $sin + pow(-1, $i) * pow($angle, (2 * $i + 1)) / fact(2 * $i + 1);
  }
  @return $sin;
}
// cos 值
@function cos($angle) {
  $cos: 0;
  $angle: rad($angle);
  // Iterate a bunch of times.
  @for $i from 0 through 10 {
    $cos: $cos + pow(-1, $i) * pow($angle, 2 * $i) / fact(2 * $i);
  }
  @return $cos;
}
// tan 值
@function tan($angle) {
  @return sin($angle) / cos($angle);
}
// 直角三角型斜边值
@function hypotenuse() {
  $w: 0.5 * $CssTiangleWidth;
  $c: $CssTiangleHeight;
  $h: sqrt(pow($c, 2) - pow($w, 2));
  @return $h;
}

.CssTiangle {
  height: #{hypotenuse()}px;
  width: #{$CssTiangleWidth}px;
  overflow: hidden;
  position: relative;
  // background-color: red;
  &-inner {
    overflow: hidden;
    position: absolute;
    margin-top: -#{1.02 * ($CssTiangleWidth - cos(30deg) * $CssTiangleWidth)}px;
    top: 50%;
    left: 50%;
    height: #{$CssTiangleHeight + 0.88 *
      ($CssTiangleWidth - cos(30deg) * $CssTiangleWidth)}px;
    width: #{$CssTiangleWidth + 0.88 *
      ($CssTiangleWidth - cos(30deg) * $CssTiangleWidth)}px;
    border-radius: 20%;
    transform-origin: center;
    transform: rotate(30deg) skewY(30deg) scaleX(0.866) translate(-50%, 50%);
    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(#0f0, #03a9f4);
    }
    &::before {
      border-radius: 20% 20% 20% 55%;
      transform: scaleX(1.155) skewY(-30deg) rotate(-30deg) translateY(-42.3%)
        skewX(30deg) scaleY(0.866) translateX(-24%);
    }
    &::after {
      border-radius: 20% 20% 55% 20%;
      transform: scaleX(1.155) skewY(-30deg) rotate(-30deg) translateY(-42.3%)
        skewX(-30deg) scaleY(0.866) translateX(24%);
    }
  }
}
