.FulePopover-table {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 90px 160px 120px;
  td {
    padding: 5px;
    > div,
    .FulePopover-btn {
      width: 100%;
    }
  }
}
.FulePopover-box {
  text-align: center;
  border: 1px solid #5a4f4f;
  background: #5a4f4f;
  height: 120px;
}
.FulePopover-btn {
  height: 30px;
  min-width: 100px;
  padding: 0 12px;
  border: 1px solid #006eff;
  line-height: 30px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  outline: 0 none;
  box-sizing: border-box;
  text-decoration: none;
  font-size: 12px;
  vertical-align: middle;
  white-space: nowrap;
  background-color: #fff;
  border-color: #ddd;
  color: #000;
}
.FulePopover-btn:hover {
  border-color: #ddd;
  color: #000;
  background-color: #f2f2f2;
}
@media screen and (max-width: 768px) {
  .m-hidden {
    display: none;
  }
}
