.cssBackPosition2-main {
  height: 100%;
  overflow: auto;
}
.cssBackPosition2-box {
  height: 100%;
  color: #fff;
  padding: 20px;
  box-sizing: border-box;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cssBackPosition2-box:nth-child(1) {
  background-color: #431616;
}
.cssBackPosition2-box:nth-child(2) {
  background-image: url(./img/bg.jpg);
  background-attachment: fixed;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: 1;
  display: flex;
}
.cssBackPosition2-box:nth-child(3) {
  background-color: #985f5f;
}
