.item {
  width: 40px;
  height: 32px;
  border-radius: 50% 50% 50% 50% / 50% 50% 45% 45%;
  background: radial-gradient(ellipse at center, white 40%, transparent 70%)
    no-repeat 60% 25% / 16% 15%;
  box-shadow: inset 7px 0 2px -5px rgba(0, 0, 0, 0.2),
    inset 6px 0 1px -5px rgba(0, 0, 0, 0.2),
    inset 0 -1px 2px rgba(250, 241, 220, 0.5),
    inset 0 -10px 5px 1px rgba(255, 255, 255, 0.3),
    inset -10px 5px 2px -10px rgba(0, 0, 0, 0.3),
    inset -10px 7px 5px -10px rgba(0, 0, 0, 0.2),
    inset 0 10px 15px -2px rgba(0, 0, 0, 0.3),
    0 2px 1px -1px rgba(245, 227, 183, 0.5),
    -1px 8px 1px -2px rgba(0, 0, 0, 0.5), -1px 10px 2px -2px rgba(0, 0, 0, 0.4),
    1px -1px 2px 0 rgba(50, 50, 50, 0.5);
}

.animation {
  position: relative;
  width: 10px;
  height: 10px;
  background: #409eff;
  border-radius: 50%;
  &:after {
    content: '';
    position: absolute;
    top: -5px;
    left: -5px;
    width: 100%;
    height: 100%;
    border: 5px solid #ff1883;
    border-radius: 50%;
    animation: antStatusProcessing 1.2s ease-in-out infinite;
  }
}

@keyframes antStatusProcessing {
  0% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    opacity: 0.5;
  }
  to {
    -webkit-transform: scale(2.4);
    transform: scale(2.4);
    opacity: 0;
  }
}
