.cssFacing-main {
  height: 100%;
  background-color: #888;
  overflow: auto;
  // // 显示单屏内容
  scroll-snap-type: y mandatory;
  // 显示多屏内容
  // scroll-snap-type: y proximity;
  // 设置帖离点偏移值
  // scroll-padding: 50px;
  box-sizing: border-box;
}
.cssFacing-header {
  position: sticky;
  top: 0;
  left: 0;
  background-color: #fff;
  padding: 12px;
  height: 50px;
  line-height: 28px;
  box-sizing: border-box;
  // display: none;
}
.cssFacing-box {
  height: 100%;
  color: #fff;
  padding: 20px;
  box-sizing: border-box;
  scroll-snap-align: start;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cssFacing-box:nth-child(1) {
  background-color: #431616;
}
.cssFacing-box:nth-child(2) {
  background-image: url(./img/bg.jpg);
  background-attachment: fixed;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: 1;
  display: flex;
}
.cssFacing-box:nth-child(3) {
  background-color: #985f5f;
}
