.cssPenetrate-main {
  height: 200px;
  overflow-y: auto;
  background-color: #431616;
  position: relative;
  color: white;

  &__dialog {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: 100px;
    width: 240px;
    background-color: #ccc;
    color: #000;
    overflow-y: auto;
    overscroll-behavior-y: contain;
  }
  &__content {
    height: 220px;
    padding: 12px;
  }
}
